@import 'mixins';

@font-face {
    font-family: 'Helveticanowdisplay';
    src: url('../fonts/HelveticaNowDisplay-Light.woff') format('woff');
    font-weight: 300;
    font-style: normal;
    font-display: swap;
}
@font-face {
    font-family: 'Helveticanowdisplay';
    src: url('../fonts/HelveticaNowDisplay-Regular.woff') format('woff');
    font-weight: 400;
    font-style: normal;
    font-display: swap;
}
@font-face {
    font-family: 'Helveticanowdisplay';
    src: url('../fonts/HelveticaNowDisplay-Medium.woff') format('woff');
    font-weight: 500;
    font-style: normal;
    font-display: swap;
}
@font-face {
    font-family: 'Helveticanowdisplay extlt';
    src: url('../fonts/HelveticaNowDisplay-ExtLt.woff') format('woff');
    font-weight: 200;
    font-style: normal;
    font-display: swap;
}
@font-face {
    font-family: 'Wulkan display';
    src: url('../fonts/WulkanDisplay-Medium.otf') format('opentype');
    font-weight: 500;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'Wulkan display';
    src: url('../fonts/WulkanDisplay-SemiBold.otf') format('opentype');
    font-weight: 600;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'Wulkan';
    src: url('../fonts/WWulkandisplay-Regular.woff') format('woff');
    font-weight: 400;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'Playfair';
    font-stretch: 10%; /* Note that font-stretch is a % of normal width */
    font-style: normal; /* See note below */
    font-weight: 300;
    src: url('../fonts/PlayfairDisplay-VariableFont_wght.ttf');
    font-variation-settings: 'wdth' 100;
}

body {
    font-family: 'Helveticanowdisplay extlt', sans-serif;
    color: rgba(255, 255, 255, 0.5);
    font-size: 1vw;
}

h1 {
    color: rgba(255, 255, 255, 0.7);
    font-family: 'Wulkan display';
    font-weight: 500;
    line-height: 1;
    position: relative;
    margin-top: 0;
    margin-bottom: 0;
    backdrop-filter: blur(0);
}

h2 {
    color: rgba(255, 255, 255, 0.7);
    font-family: 'Wulkan display', sans-serif;
    font-weight: 500;
    letter-spacing: 1px;
    margin-top: 0;
    margin-bottom: 0;
    margin-left: -5px;
    transform-style: preserve-3d;
    backdrop-filter: blur(0);
}

h3 {
    // text-transform: uppercase;
    // font-size: 12px;
    font-family: 'Wulkan display', sans-serif;
    color: rgba(255, 255, 255, 1);
    font-weight: normal;
    line-height: 1;
    padding: 0;
    margin: 0;
    backdrop-filter: blur(0);
}

h4 {
    font-family: 'Helveticanowdisplay extlt', sans-serif;
    font-weight: 300;
    color: rgba(255, 255, 255, 0.5);
    margin: 0;
    backdrop-filter: blur(0);
}

p {
    color: rgba(255, 255, 255, 0.5);
    font-family: 'Helveticanowdisplay extlt', sans-serif;
    letter-spacing: 0.2px;
    line-height: 1.3;
    margin: 0;
    backdrop-filter: blur(0);
}

a {
    color: rgba(255, 255, 255, 0.5);
    text-decoration: none;
    backdrop-filter: blur(0);
}

span,
aside {
    backdrop-filter: blur(0);
}

.quote {
    font-family: 'Wulkan display', sans-serif;
}
